}

.radio input[type="radio"] {
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
  border: none;
  margin: 0;
  padding: 0;
  filter: alpha(opacity=0);
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  outline: none;
}

.radio label {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  border-radius: 100%;
  background-color: #edeff2;
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}

.radio label span {
  font-size: 20px;
}

.radio.is-active label {
  color: #fff;
  background-color: #14a8df;
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}

.checkbox {
  position: relative;
  cursor: pointer;
}

.checkbox input[type="checkbox"] {
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
  border: none;
  margin: 0;
  padding: 0;
  filter: alpha(opacity=0);
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  outline: none;
}

.checkbox label {
  cursor: pointer;
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.checkbox__icon {
  position: relative;
  width: 33px;
  margin-right: 12px;
}

.checkbox__icon span {
  display: block;
  width: 100%;
  height: 15px;
  border-radius: 2em;
  background-color: #edeff2;
}

.checkbox__icon span:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  border-radius: 100%;
  background-color: #666;
  transition: background-color 0.3s ease-in-out, left 0.3s ease-in-out;
}

.is-active .checkbox__icon span:before {
  left: calc(100% - 12px);
}

.is-active.checkbox--irisBlue .checkbox__icon span:before {
  background-color: #14a8df;
  transition: background-color 0.3s ease-in-out, left 0.3s ease-in-out;
}

