  transition: background-color 0.3s ease-in-out, left 0.3s ease-in-out;
}

.is-active.checkbox--violetBlue .checkbox__icon span:before {
  background-color: #ab499b;
  transition: background-color 0.3s ease-in-out, left 0.3s ease-in-out;
