// HEADER
// ===============
.header {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 15px;


  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 25px; }


  &__container {
    display: flex;
    align-items: center; }


  &_logo {
    &--tab {
      height: 30px; } }


  &__row {
    &-0 {
      width: 285px;
      @include rmin($mobile-s) {
        width: auto;
        padding-left: 0;
        padding-right: 30px; }
      @include rmin($desktop) {
        padding-right: 30px;
        padding-left: 20px; }
      @include rmin($wide) {
        width: 285px; }

      img {
        cursor: pointer;
        margin-top: 6px;
        @include rmin($mobile-s) {
          height: 30px;
          margin-top: 0; }
        @include rmin($desktop) {
          height: auto;
          margin-top: 6px; } } }

    &-1 {
      padding-top: 5px; }


    &-3 {
      width: 180px;
      text-align: right;
      @include rmin($mobile-s) {
        display: flex;
        width: auto;
        align-items: center; }
      @include rmin($tablet) {
        display: block;
        width: auto;
        padding-left: 15px; }
      @include rmin($desktop) {
        width: 180px; } } }


  &__phone {
    position: relative;
    top: -5px;
    color: $colorWhite;
    font-weight: 500;
    @include transition(text-shadow);
    &:hover {
      text-shadow: 0 0 20px rgba($colorWhite, 0.75);
      @include transition(text-shadow); }

    @include rmin($mobile-s) {
      top: 0;
      font-size: 12px; }
    @include rmin($mobile) {
      font-size: 20px; }
    @include rmin($tablet) {
      top: -5px;
      font-size: 16px; }
    @include rmin($desktop) {
      font-size: 20px; }

    &--tab {
      @include r($mobile-max) {
        display: block;
        text-align: center;
        font-size: 30px;
        color: $colorDimGray;
        padding-top: 45px;
        padding-bottom: 45px; } } } }
